.ReactTokenInput {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  -ms-flex-pack: center;
      justify-content: center;
  max-width: 700px;
  margin: 0 0 1rem;
  border-bottom: none;
}

.ReactTokenInput__loading-label {
  font-family: "Open Sans", sans-serif;
  display: block;
  padding: 5px 0;
  color: #ccc;
  text-align: center;
  font-size: 12px;
}

.ReactTokenInput__token {
  font-family: "Open Sans", sans-serif;
  border-radius: 3px;
  background: #14b3ff;
  color: #fff !important;
  font-size: 12px;
  line-height: 1.5;
  margin: 5px 5px 5px 0;
  padding: 2px 10px;
  border-radius: 10px;
}

.ReactTokenInput__token-orange {
  font-family: "Open Sans", sans-serif;
  border-radius: 3px;
  background: #e14c31;
  color: #fff !important;
  font-size: 12px;
  line-height: 1.5;
  margin: 5px 5px 5px 0;
  padding: 2px 10px;
  border-radius: 10px;
}

.ReactTokenInput__token-orange .icon-times {
  cursor: pointer;
  margin: 0 3px;
  font-size: 1.2em;
}

.ReactTokenInput__token .icon-times {
  cursor: pointer;
  margin: 0 3px;
  font-size: 1.2em;
}

.ReactTokenInput__input-col:first-child:last-child {
  width: 100%;
}

.ReactTokenInput__input {
  width: 100%;
  text-align: center;
  height: calc(2em + 3px);
  border: 0;
  padding: 10px;
  border-radius: 10px;
}

.ReactTokenInput__options-list {
  font-family: "Open Sans", sans-serif;
  width: auto;
  display: none;
  position: absolute;
  top: 100%;
  background-color: #fff;
  left: 0;
  z-index: 1;
  max-height: 150px;
  overflow: scroll;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px;
  padding-left: 18px;
  padding-right: 18px;
}
.ReactTokenInput--open .ReactTokenInput__options-list {
  display: block;
}

.ReactTokenInput__option {
  text-align: center;
  display: block;
  padding: 0.6em;
  border-bottom: 1px solid #ccc;
  cursor: default;
}


